@import url('https://fonts.googleapis.com/css?family=Audiowide');

.terms-div {
    width: 80%;
    height: 100%;
    font-family: "Audiowide";
}

.terms {
    width: 99.75%;
    border: .25vh solid #040507;
    border-top: none;
    background-color: white;
    height: 70vh;
    overflow-y: scroll;
    font-family:'Times New Roman', Times, serif;
    padding: 0px;
}

.terms-text {
    padding: 2.5%;
}

.terms-header {
    text-align: center;
    height: auto;
    background-color: #252830;
    border-top-left-radius: 1vh;
    border-top-right-radius: 1vh;
}

.terms-h3 {
    color: white;
    margin-bottom: 0vh;
    font-weight: 100;
}

.terms-decision {
    display: inline-flex;
    justify-content: space-evenly;
    border-bottom-left-radius: 1vh;
    border-bottom-right-radius: 1vh;
    background-color: #252830;
    border-top: none;
    width: 100%;
    height: 8vh;
}

.terms-buttons {
    display: inline-flex;
    justify-content: space-evenly;
    padding: 1vh;
    width: 100%;
}

/* Overrides */
.endzone-btn:hover .btn-text {
    color: black !important;
}

.btn-text {
    border-right: .5vh solid black !important;
}

@media (max-width: 600px) {
    .terms-decision {
        height: auto;
    }

    .terms-buttons {
        display: inline-block;
        text-align: center;
        margin: auto;
        width: 100%;
    }

    .term-button {
        width: auto;
        margin: 1vh;
    }
}